<template>
    <div class="content-wrap">
        <div class="topContent">
            <div class="title">基本信息</div>
            <div class="items">
                <img class="avatar" :src="userDetailInfo.image_url ? userDetailInfo.image_url : require('../../assets/img/user.jpg')" alt="">
                <div class="item">
                    <span>用户昵称：</span>
                    <span>{{userDetailInfo.nick_name ? userDetailInfo.nick_name : '-'}}</span>
                </div>
                <div class="item">
                    <span>用户ID：</span>
                    <span>{{userDetailInfo.user_id ? userDetailInfo.user_id : '-'}}</span>
                </div>
                
            </div>
            <div class="items">
                <div class="item">
                    <span>手机号：</span>
                    <span>{{userDetailInfo.user_mobile_phone ? userDetailInfo.user_mobile_phone : '-'}}</span>
                </div>
                <div class="item">
                    <span>注册时间：</span>
                    <span>{{userDetailInfo.create_time | getTime }}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>邮箱：</span>
                    <span>{{userDetailInfo.user_email ? userDetailInfo.user_email : '-'}}</span>
                </div>
                <div class="item">
                    <span>注册渠道：</span>
                    <span>{{userDetailInfo.client ? userDetailInfo.client : '-'}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>最近登陆时间：</span>
                    <span>{{userDetailInfo.login_time | getTime }}</span>
                </div>
            </div>
        </div>
        <div class="bottomContent">
            <div class="title">消费信息：</div>
            <div class="items">
                <div class="item">
                    <span>消费次数：</span>
                    <span>{{userDetailInfo.num_pay ? userDetailInfo.num_pay + '次' : '-'}}</span>
                </div>
                <div class="item">
                    <span>累计消费金额：</span>
                    <span>{{userDetailInfo.total_amout ? (userDetailInfo.total_amout/100).toFixed(2) : '-'}}</span>
                </div>
            </div>
            <div class="items"> 
                <div class="item">
                    <span>购机次数(含续费)：</span>
                    <span>{{userDetailInfo.num_pay_pad ? userDetailInfo.num_pay_pad + '次' : '-'}}</span>
                </div>
                <div class="item">
                    <span>购机总金额：</span>
                    <span>{{userDetailInfo.total_amout_pad ? (userDetailInfo.total_amout_pad/100).toFixed(2) : '-'}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>付费服务次数：</span>
                    <span>{{userDetailInfo.num_pay_extend ? userDetailInfo.num_pay_extend + '次' : '-'}}</span>
                </div>
                <div class="item">
                    <span>付费服务总金额：</span>
                    <span>{{userDetailInfo.total_amout_extend? (userDetailInfo.total_amout_extend/100).toFixed(2) : '-'}}</span>
                </div>
            </div>
        </div>
        <div class="bottomContent">
            <div class="title">设备信息：</div>
            <div class="items">
                <div class="item">
                    <span>购买总时长(含续费)：</span>
                    <span>{{userDetailInfo.total_device_time | getDate }}</span>
                </div>
                <div class="item">
                    <span>使用总时长：</span>
                    <span>{{userDetailInfo.used_device_time | getDate}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>有效设备(台)：</span>
                    <span>{{userDetailInfo.num_device ? userDetailInfo.num_device + '台' : '-'}}</span>
                </div>
                <div class="item">
                    <span>即将到期设备(台)：</span>
                    <span>{{userDetailInfo.num_expiring ? userDetailInfo.num_expiring + '台' : '-'}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>启动中设备(台)：</span>
                    <span>{{userDetailInfo.num_using ? userDetailInfo.num_using + '台' : '-'}}</span>
                </div>
                <div class="item">
                    <span>常用渠道：</span>
                    <span>{{userDetailInfo.favorite_channel ? userDetailInfo.favorite_channel : '-'}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>上传应用数：</span>
                    <span>{{userDetailInfo.num_upload_apk ? userDetailInfo.num_upload_apk + '个' : '-'}}</span>
                </div>
                <div class="item">
                    <span>上传文件数：</span>
                    <span>{{userDetailInfo.num_upload_file ? userDetailInfo.num_upload_file + '个' : '-'}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>群控次数：</span>
                    <span>{{userDetailInfo.num_group_using ? userDetailInfo.num_group_using + '次' : '-'}}</span>
                </div>
                <div class="item">
                    <span>群控总时长：</span>
                    <span>{{userDetailInfo.total_group_time ? userDetailInfo.total_group_time : '-'}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>付费服务(TOP3)：</span>
                    <span>{{userDetailInfo.top3_services ? userDetailInfo.top3_services : '-'}}</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { parseTime, formatDate } from '../../utils/index';
import { userDetailInfo } from '../../api/user';
import { mapState } from 'vuex'
export default {
    data() {
        return {
            orderInfo: {},
            userDetailInfo: {},
            userId: ''
        }
    },
    computed: {
        ...mapState({
            orders: state => state.order.orders
        }),
    },
    filters: {
        getTime(time) {
            if(time) return parseTime(time);
			return '-'
		},
        getDate(time) {
            if(time) return formatDate(time);
			return '-'
		},
    },
    mounted() {
        console.log(this.orders)
        this.userId = this.$route.query.userId
        console.log(this.userId)
        this.getUserDetailInfo();
        // this.orderList.push(this.orderInfo);
    },
    methods: {
        getUserDetailInfo() {
            let params = new Object();
            params.userId  = Number(this.userId);
            userDetailInfo(params)
            .then(res => {
                if (res.code == 200) {
                  this.userDetailInfo = Object.assign(res.data.base_info, res.data.consuming, res.data.device_info)
                }
            })
            .catch(error => {
                console.log(error)
            });
        },
    }
}
</script>
<style lang="less" scoped>
.content-wrap{
    margin: 25px;
    .bottomContent{
        margin-top: 30px;
    }
    .topContent,.bottomContent{
        padding: 20px 15px;
        border: 1px solid #eee;
        .title{
            border-left: 3px solid #409EFF;
            padding-left: 6px;
            margin-bottom: 20px;
        }
        .items{
            padding: 0 0 25px 6px;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            padding-left: 60px;
            position: relative;
            .avatar{
                width: 40px;
                height: 40px;
                border-radius: 50%;
                position: absolute;
                left: 10px;
            }
            .item{
                width: calc(100%/2)
            }
        }
    }
}
</style>


